.movie {
    position: relative;
    width: 340px;
    margin: 20px;
    cursor: pointer;
}
.movieImg {
    height: 180px;
    width: 340px;
}
.movieImg img {
    width: 100%;
    height: 100%;
    -webkit-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
}
.movie:hover img {
    -webkit-transform: scale(1.15);
        -ms-transform: scale(1.15);
            transform: scale(1.15);
}
.movieDetails {
    position: absolute;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
    -webkit-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    font-size: 14px;
}
.mDetails {
    -ms-flex: 2 1;
        flex: 2 1;
}
.mRating {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background: #c11c25;
    padding: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
    left: -14px;
}
.movie:hover .movieDetails {
    bottom: 0px;
    -webkit-transform: scale(1.1) translate(-7px, 11px);
        -ms-transform: scale(1.1) translate(-7px, 11px);
            transform: scale(1.1) translate(-7px, 11px)
}
.avg-rating {
    background-color: red;
    height: 40px;
    width: 40px;
    text-align: center;
    vertical-align: middle;
    color: white;
    position: relative;
    margin-left: auto;
    margin-right: 16px;
    font-size: 19px;
    line-height: 40px;
    border-radius: 100px;
    text-align: center;
}


